import { NavLink } from "react-router-dom";
import './header.css';
function Header (){

    return (
        <header class="header">  
            <div class="logo">
                <NavLink to="/" style={{ textDecoration: 'none' }}>
                    Kochetkov
                </NavLink>
            </div>
        <input class="menu-btn" type="checkbox" id="menu-btn" />
        <label class="menu-icon" for="menu-btn"><span class="navicon"></span></label>
            <ul class="menu">
            <li> 
            <NavLink to="/" >
                Home
                </NavLink>
                </li>
            <li> 
            <NavLink to="/skills" >
                Skills
                </NavLink>
                </li>
            <li>     
            <NavLink to="/projects"  >
                Projects
                </NavLink>
                </li>
            <li> 
            <NavLink to="/contacts" >
                Contacts
                </NavLink>
                </li>
            </ul>
        </header>
    );
}

export default Header;