
import { NavLink } from "react-router-dom";
import { projects } from "./../collection/projectsList";
import './../styles/project.css';

const Projects =() =>{
    return (
    <>  
     <div class="main">
        <ul class="grids">
            {projects.map(item => 
            <li class="grids_item">
                <div class="grid">
                    <div class="grid_image">
                        <img src={item.img} alt="img" />
                        <div class="grid_content">   
                            <h2 class="grid_title"> Project - {item.type}</h2>
                            <p class="grid_text">{item.title}</p> 
                            <NavLink to={`/project/${item.id}`}   style={{ textDecoration: 'none' }}>                             
                                <button class="btn grid_btn">Read More</button>                      
                            </NavLink>
                        </div> 
                    </div>
                </div>
            </li>)}
        </ul>
        </div>  
       
    </>
    );
}

export default Projects;