import { useParams } from "react-router-dom";
import { projects } from "./../collection/projectsList"
import { NavLink } from "react-router-dom";
import './../styles/project.css';

const Project=()=>{

const {id} = useParams();
const project = projects[id];

    return (
        <> 
        <div className="app">
        <div class="main">
        <ul class="grids">
            <li class="grids_item">
            </li> 
            <li class="grids_item">
                <div class="grid">
                    <div class="grid_content">
                        <h2 class="grid_title">Project - {project.type}</h2>
                        <p class="grid_text">{project.title}</p>
                        <h2 class="grid_title">Stack - {project.stack}</h2>           
                        <p class="grid_text">Description: {project.description}</p>                
                        <p class="grid_title">Year: {project.year}</p>
                        <br/>
                            {project.link && (
                                <a href={project.link}   style={{ textDecoration: 'none' }}>                             
                                    <button class="btn grid_btn">Link</button>                      
                                </a> 
                            )
                             }
                        <br/>
                            <NavLink to={`/projects/`}   style={{ textDecoration: 'none' }}>                             
                                <button class="btn grid_btn">Back</button>                      
                            </NavLink>   
                         </div>
                </div>
            </li>
            </ul>
            </div>
            </div>
        </>
    );
}
export default Project;